<template>
  <div>
      <div class="content-center" style="margin-top: 10px;">
        <div class="container">
          <div v-show="show_error">
            <h1 class="title">Gagal!</h1>
            <div class="text-center">
              {{ error_msg }} <br>
            </div>
          </div>
          <div v-show="show_rules">
            <h1 class="title">Selamat Datang di Tes Online <br>Universitas Dayanu Ikhsanuddin</h1>
            <div class="text-center">Di Tes Online ini anda akan mengisi 7 soal umum untuk masuk ke Universitas Dayanu Ikhsanuddin</div>
            <div class="row">
                <div class="col-md-4"></div>
                <div class="col-md-4"><a href="#" class="btn btn-primary btn-round btn-lg btn-block" @click="beginTest()">Mulai Tes!</a></div>
                <div class="col-md-4"></div>
              </div>
          </div>
          <div v-show="show_question">
            <div v-for="question in questions" :key="question.id">
              <div class="row pt-5">
                <div class="col-lg-12 col-md-12" style="white-space: pre-line; font-size:24px;">
                {{ question.teks_soal }}
                </div>
              </div>
              <div class="row">
                <div class="col-sm-6 col-lg-3" style="font-size:18px;">
                  <n-radio v-model="question.answer" label="0">{{ question.opsi_a }}</n-radio>
                  <n-radio v-model="question.answer" label="1">{{ question.opsi_b }}</n-radio>
                  <n-radio v-model="question.answer" label="2">{{ question.opsi_c }}</n-radio>
                  <n-radio v-model="question.answer" label="3">{{ question.opsi_d }}</n-radio>
                </div>
              </div>
            </div>
            <div class="row">
              <div class="col-md-8">
              </div>
              <div v-if="lastPage==false" class="col-md-4">
                <n-button type="primary" round @click="nextQuestion()">Selanjutnya</n-button>
              </div>
              <div v-if="lastPage==true" class="col-md-4">
                <n-button type="primary" round @click="finishTest()">Selesai</n-button>
              </div>
            </div>
          </div>
          <div v-show="showResult" style="text-align: center">
            <h1 class="title">Nilai Tes Anda: </h1>
            <h1 class="title" style="font-size: 72px;">{{ testResult.score }}</h1>
            <div v-if="testResult.score >= 50">
              <p style="font-size: 18px; color: green; font-weight: bold">Selamat! Anda lulus ujian masuk Universitas Dayanu Ikhsanuddin!</p>
              <p style="font-size: 18px; color: green; font-weight: bold">Tahap pendaftaran selanjutnya telah dikirimkan pada E-Mail dan No. HP Terdaftar anda.</p>
            </div>
            <div v-if="testResult.score < 50">
              <p style="font-size: 18px; color: red; font-weight: bold">Maaf! Anda tidak lulus ujian</p>
              <p style="font-size: 18px; color: red; font-weight: bold">Jangan patah semangat ya!</p>
            </div>
          </div>
        </div>
      </div>
  </div>
</template>
<script>
import { Button, Radio, Tabs, TabPane, Card } from '@/components';
import moment from 'moment';
export default {
  name: 'test',
  bodyClass: 'profile-page',
  components: {
    Tabs,
    TabPane,
    Card,
    [Radio.name]: Radio,
    [Button.name]: Button,
  },
  data() {
      return {
          session_id: null,
          sessionData: null,
          time_start: '',
          d_time_start: null,
          show_rules: true,
          show_question: false,
          currentPage: 0,
          totalQuestion: 0,
          questions: {},
          isFinished: false,
          lastPage: false,
          testResult: {},
          showResult: false,
          show_error: false,
          error_msg: '',
      }
  },
  methods: {
      async finishTest(){
        // Kirim jawaban yang sudah dijawab
        if(this.questions.length > 0){
          for(var i = 0; i < this.questions.length; i++){
            await this.axios.post('set_answer/' + this.session_id, { question_id: this.questions[i].id, answer: this.questions[i].answer });
          }
        }
        this.show_question = false;
        let { data } = await this.axios.get('finish_test/' + this.session_id);
        this.testResult = data.testResult;
        this.showResult = true;
        this.isFinished = true;
      },
      async showQuestion(page){
        // Kirim jawaban yang sudah dijawab
         if(this.questions.length > 0){
          for(var i = 0; i < this.questions.length; i++){
            await this.axios.post('set_answer/' + this.session_id, { question_id: this.questions[i].id, answer: this.questions[i].answer });
          }
        }
        // Ambil current answer dan kirim ke server
        this.show_question = false;
        let { data } = await this.axios.post('get_question/' + this.session_id, {page_no: page});
        this.questions = data.questions;
        this.lastPage = data.last_page;
        this.show_question = true;
        this.currentPage = page;
      },
      async beginTest(){
        this.show_rules = false;
        this.showQuestion(parseInt(this.sessionData.halaman_sekarang));
      },
      async getSessionData(reglink){
        let { data }  = await this.axios.get('getSession/' + reglink);
        if(data.error){
          this.show_error = true;
          this.error_msg = data.error_msg;
          this.show_rules = false;
          return;
        }
        this.sessionData = data.sessionData;
        this.session_id = reglink;
        if(this.sessionData.nilai !== null){
          this.show_rules = false;
          this.showResult = true;
          this.isFinished = true;
          this.testResult = { score: this.sessionData.nilai };
        }
      },
      nextQuestion(){
        this.showQuestion(this.currentPage+1);
      },
      transform(props) {
        Object.entries(props).forEach(([key, value]) => {
          // Adds leading zero
          const digits = value < 10 ? `0${value}` : value;

          props[key] = `${digits}`;
        });

        return props;
      },
      pad(n, width, z) {
        z = z || '0';
        n = n + '';
        return n.length >= width ? n : new Array(width - n.length + 1).join(z) + n;
      }
    },
    created(){
        this.getSessionData(this.$route.params.reglink);
    },
};
</script>
<style>
li:not(:last-child) {
    margin-bottom: 15px;
}
</style>
