import Vue from 'vue';
import Router from 'vue-router';
import Test from './pages/Test.vue';


Vue.use(Router);

const router = new Router({
    linkExactActiveClass: 'active',
    routes: [{
        path: '/test/:reglink',
        name: 'index',
        components: { default: Test },
    }],
    scrollBehavior: to => {
        if (to.hash) {
            return { selector: to.hash };
        } else {
            return { x: 0, y: 0 };
        }
    }
});

export default router;